.article-detail {
    padding-bottom: 80px;

    &__title {
        @include page-title-one;
    }
    &__date {
        font-weight: 600;
        margin-top: 20px;
        margin-bottom: 20px;
    }
    &__banner {
        img {
            max-height: 500px;
            width: 100%;
            object-fit: cover;
        }
    }
}
@media(max-width:1199px){
    .article-detail {
        margin: 0 20px;
        margin-top: 28px;
    }
}
@media(max-width: 1024px){
    .article-detail__title {
        font-size: 46px;
        margin-bottom: 35px;
    }
}
@media(max-width: 967px){
    .article-detail__title {
        font-size: 42px;
        margin-bottom: 25px;
    }
}
@media(max-width: 860px){
    .article-detail__title {
        font-size: 38px;
        margin-bottom: 20px;
    }
}
@media(max-width:768px){
    .article-detail {
        margin-top: 15px;
        padding-bottom: 50px;
    }

}
@media(max-width: 630px){
    .article-detail__title {
        font-size: 34px;
    }
}
@media(max-width: 585px){
    .article-detail__title {
        font-size: 30px;
    }
}
@media(max-width: 480px){
    .article-detail__title {
        font-size: 28px;
    }
}
@media(max-width: 360px){
    .article-detail__title {
        font-size: 26px;
    }
}
@media(max-width: 330px){
    .article-detail__title {
        font-size: 24px;
    }
}