.faq {
    background-color: #fff;
    
    .container {
        padding: 65px;
        padding-bottom: 165px;
    }

    &__title {
        @include font-roboto;
        font-size: 38px;
        font-weight: 700;
        line-height: 120%;
        letter-spacing: -0.05em;
        color: $black-color;
        text-shadow: $text-shadow-dark;
        margin: 0;
        margin-bottom: 45px;
    }
    &__body {
        display: none;
    }
    &__item {
        border-bottom: 1px solid #ababab;
        transition: $transition;
        cursor: pointer;

        &.active {
            .faq__accr-control{
                transform: rotate(-45deg);
            }
            .faq__accr-name {
                color: $pink-color;
            }
        }

        &:hover {
            .faq__accr-name {
                color: $pink-color;
            }
        }
    }
    &__head {
        padding: 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__body {
        background-color: $grey-color;
        padding: 25px;
    }
    &__accr-name {
        @include font-roboto;
        font-size: 32px;
        font-weight: 400;
        line-height: 120%;
        color: $black-color;
        letter-spacing: -0.04em;
        transition: $transition;
    }
    &__accr-text {
        @include font-roboto;
        @include main-page-text;
        letter-spacing: -0.04em;
        color: $black-color;
    }
    &__accr-control {
        width: 50px;
        height: 50px;
        background-repeat: no-repeat;
        background-size: contain;
        transition: $transition;
    }
}
@media(max-width:975px){
    .faq__title {
        font-size: 34px;
        margin-bottom: 35px;
    }
    .faq__accr-name {
        font-size: 28px;
    }
    .faq__accr-text {
        font-size: 22px;
    }
}
@media(max-width:860px){
    .faq .container {
        padding: 50px;
        padding-bottom: 145px;
    }
}
@media(max-width: 678px){
    .faq .container {
        padding: 40px;
        padding-bottom: 100px;
    }
    .faq__head {
        padding: 15px;
    }
    .faq__accr-control {
        width: 40px;
        height: 40px;
    }
    .faq__body {
        padding: 20px;
    }
    .faq__title {
        font-size: 28px;
    }
    .faq__accr-name {
        font-size: 22px;
    }
    .faq__accr-text {
        font-size: 18px;
    }
}
@media(max-width: 480px){
    .faq .container {
        padding: 30px;
        padding-bottom: 80px;
    }
    .faq__title {
        font-size: 26px;
        margin-bottom: 20px;
    }
    .faq__head {
        padding: 15px 0;
    }
    .faq__accr-text {
        font-size: 16px;
    }
}
@media(max-width: 380px){
    .faq__title {
        font-size: 24px;
        margin-bottom: 15px;
    }
    .faq__accr-name {
        font-size: 20px;
    }
    .faq__accr-text {
        font-size: 14px;
    }
    .faq__accr-control {
        width: 30px;
        height: 30px;
    }
}