@import 'mixins.scss';
@import 'vars.scss';

.header {

    &__mobile-btns {
        display: flex;
        flex-direction: column;
        padding-left: 30px;

        a {
            color: #fff;
            font-size: 20px;

            span {
                border-bottom: 1px solid #fff;
            }
        }
    }
    
    &__top {
        padding-top: 35px;
        padding-bottom: 20px;
        background-color: $grey-color;

        .container {
            display: flex;
            justify-content: space-between;
        }

        &-container {
            display: flex;
            align-items: center;
            column-gap: 25px;
        }
    }
    &__logo {
        height: 80px;

        img {
            object-fit: contain;
        }
    }
    &__collection {
        width: 35px;
        height: 35px;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        cursor: pointer;
        transition: $transition;
        background-image: url("../img/icons/LOGO-black.png");

        &:hover {
            background-image: url("../img/icons/LOGO-pink.png");
        }
    }
    &__login {
        width: 35px;
        height: 35px;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        background-image:url("../img/icons/personal-kabinet.png");
        transition: $transition;

        &:hover {
            background-image:url("../img/icons/personal-kabinet-pink.png");
        }
    }
    &__search{
        display: flex;
        overflow: hidden;
        align-items: center;
        cursor: pointer;

        input[type="submit"]{
            display: none;
        }
        &-mobile {
            padding-left: 30px;
            display: flex;
            .header__search-input {
                right: 0;
                display: none;
                transition: none;
                width: 100%;
                
                input[type=text] {
                    height: 35px;
                    border-bottom: 1px solid #ffffff;
                    color: #fff;

                    &::placeholder {
                        color: #a8a8a88a;
                    }
                    &:hover {
                        border-color: #ffffff;
                    }
                }
            }
            input[type="submit"]{
                display: none;
            }
            .header__search-icon {
                position: absolute;
                right: 30px;
                width: 35px;
                height: 35px;
                background-image: url("../img/icons/search-icon-white.png");
                transition: none;

                &:hover {
                    background-image: url("../img/icons/search-icon-white.png");
                }
            }
        }
        &-input {
            transition: $transition;
            position: relative;
            right: -120%;
            width: 350px;
            max-width: 400px;

            &.active {
                right: 0;
            }
            & input[type="text"]{
                width: 100%;
                height: 35px;
                border: none;
                padding: 5px;
                padding-left: 10px;
                outline: none;
                background-color: transparent;
                border-bottom: 1px solid $black-color;

                &::placeholder {
                    text-transform: uppercase;
                    color: #535353;
                    font-size: 16px;
                }
                &:focus,
                &:hover {
                    border-color: $pink-color;
                }
            }
        }
        &-icon {
            width: 35px;
            height: 35px;
            background-repeat: no-repeat;
            background-size: contain;
            position: relative;
            z-index: 2;
            background-image: url("../img/icons/search-icon.png");
            background-position: center;
            transition: $transition;

            &:hover{
                background-image: url("../img/icons/search-icon-hover.png");
            }
        }
    }
    &__bottom {
        width: 100%;
        background-color: $black-color;
        position: relative;
    }
    &__burger-btn {
        display: none;
        position: relative;
        margin-top: 10px;
        height: 30px;

        &.active {
            .header__burger-icon {
                background: none;
            }

        }
    }
    &__burger-icon {
        display: block;
        position: relative;
        background-color: $black-color;
        height: 4px;
        width: 100%;
        border-radius: 20px;
        -webkit-transition: background 0s .3s, width .3s .3s;
        -o-transition: background 0s .3s, width .3s .3s;
        transition: background 0s .3s, width .3s .3s;
        margin: 20px 0;

        &::before,
        &::after {
            content: "";
            position: absolute;
            background-color: $black-color;
            right: 0;
            display: block;
            height: 100%;
            width: 100%;
            border-radius: 50px;
            -webkit-transition: bottom .3s .3s, width .3s .6s, -webkit-transform .3s 0s;
            transition: bottom .3s .3s, width .3s .6s, -webkit-transform .3s 0s;
            -o-transition: transform .3s 0s, bottom .3s .3s, width .3s .6s;
            transition: transform .3s 0s, bottom .3s .3s, width .3s .6s;
            transition: transform .3s 0s, bottom .3s .3s, width .3s .6s, -webkit-transform .3s 0s
        }   
        &::after {
            bottom: 10px
        }
        &:before {
            bottom: -10px;
            width: 20px;
        }

    }
    &__menu {
        margin: 0;
        display: flex;
        width: 100%;

        &-link {
            margin: 0;
            display: flex;
            padding-top: 25px;
            padding-bottom: 25px;
            padding-left: 0;
            justify-content: center;
            align-items: center;
            width: 229px;
            border-left: 1px solid #fff;
            cursor: pointer;
            transition: $transition;

            &:hover {
                border-color: #ff51ce;

                & + .header__menu-link {
                    border-color: #ff51ce;
                }
            }

            a {
                @include font-roboto;
                color: #f8f8f8;
                position: relative;
                font-size: 18px;
                line-height: 110%;
                text-align: center;
                font-weight: 500;
                text-transform: uppercase;

                &::before {
                    content: "";
                    position: absolute;
                    left: 0;
                    bottom: -6px;
                    width: 100%;
                    height: 2px;
                    background-color: #fff;
                    transform: scaleX(0);
                    transition: $transition;
                }
                &:hover::before {
                    transform: scaleX(1);
                }
            }

            &:first-child {
                border-left: 0;
            }
        }
    }
    &__mobile {
        display: none;
        min-width: 40px;
        margin-left: 10px;
        cursor: pointer;
    }
    &__mobile-wrapper {
        display: none;
        padding-top: 30px;
        width: 0;
        position: absolute;
        height: 100vh;
        background-color: $black-color;
        z-index: 15;
        right: -100%;
        transition: $transition;
        overflow-x: hidden;
        opacity: 0;

        &.active {
            right: 0;
            opacity: 1;
            width: 100%;
        }
    }
    &__mobile-link {
        margin-bottom: 15px;
        a{
            font-size: 20px;
            color: #fff;
            border-bottom: 1px solid #fff;
        }
    }
}
.header__burger-btn.active .header__burger-icon:after,
.header__burger-btn.active .header__burger-icon:before {
  bottom: 0;
  -webkit-transition: bottom .3s .3s, width .3s 0s, -webkit-transform .3s .6s;
  transition: bottom .3s .3s, width .3s 0s, -webkit-transform .3s .6s;
  -o-transition: transform .3s .6s, bottom .3s .3s, width .3s 0s;
  transition: transform .3s .6s, bottom .3s .3s, width .3s 0s;
  transition: transform .3s .6s, bottom .3s .3s, width .3s 0s, -webkit-transform .3s .6s
}

.header__burger-btn.active .header__burger-icon:after {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg)
}

.header__burger-btn.active .header__burger-icon:before {
  width: 100%;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg)
}

@media(max-width:1160px){
    .header__top {
        padding-left: 15px;
        padding-right: 15px;
    }
}
@media(max-width:1120px){
    .header__top {
        padding-left: 15px;
        padding-right: 15px;
    }
}
@media(max-width:920px){
    .header__menu-link a {
        font-size: 14px;
    }
    .header__menu-link {
        padding-top: 15px;
        padding-bottom: 15px;
    }
    .header__search-input {
        width: 300px;
    }
}
@media(max-width:768px){
    .header__logo {
        height: 50px;
    }
    .header__top {
        padding-top: 15px;
    }
    .header__top-container {
        column-gap: 10px;
    }
    .header__login,
    .header__collection,
    .header__search-icon {
        width: 25px;
        height: 25px;
    }
    .header__search-input input[type=text] {
        height: 25px;

        &::placeholder {
            font-size: 14px;
        }
    }
    .header__search-input {
        width: 250px;
    }
}
@media(max-width:620px){
    .header__menu {
        display: none;
    }
    .header__burger-btn {
        display: block;
    }
    .header__bottom {
        background-color: #F8F8F8;
    }
    .header__mobile {
        display: block;
    }
    .header__search {
        display: none;
    }
    .header__login, .header__collection {
        width: 35px;
        height: 35px;
    }
    .header__mobile {
        margin-left: 20px;
    }
    .header__top-container {
        margin-left: auto;
    }
    .header__mobile-wrapper {
        display: block;
    }
}
@media(max-width:530px){
    .header__search-mobile .header__search-input {
        width: 75%;
    }
    .header__top-container {
        display: none;
    }
}
@media(max-width:380px){

}
@media(max-width:360px){
    .header__search-mobile .header__search-icon {
        right: 15px;
    }
}