.modal-del-find {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: none;
    justify-content: center;
    align-items: center;
    background-color: #000000a1;

    &__body {
        background-color: #fff;
        padding: 30px;
        width: 700px;
        position: relative;
    }
    &__title {
        font-size: 42px;
        text-transform: uppercase;
        font-weight: 700;
        margin-bottom: 10px;
    }
    &__desc {
        font-size: 20px;
        margin-bottom: 50px;
    }
    &__close {
        width: 40px;
        height: 40px;
        background-image: url("../img/icons/icon-close.png");
        background-repeat: no-repeat;
        background-size: contain;
        right: 10px;
        top: 10px;
        background-position: center;
        position: absolute;
        cursor: pointer;
    }
    &__btns {
        display: flex;
        column-gap: 30px;

        .btn {
            font-size: 22px;
            text-transform: uppercase;
            margin-bottom: 0;
            min-width: 220px;
        }
    }
}